/* The container must be positioned relative: */
.mg-dropdown {
  position: relative;
  margin-bottom: 1rem;
  display: inline-block;
}

/*hide original SELECT element: */
.mg-dropdown input {
  display: none;
}

.mg-dropdown--content {
  width: 100%;
  min-width: max-content;
  box-shadow: var(--mg-control-shadow);
  margin-left: auto;
  margin-right: auto;
  border: 1px solid;
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: none;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  background-color: var(--mg-color-initial);
  top: 100%;
  z-index: 999;
  border-color: var(--mg-color-quaternary);
  border-radius: var(--mg-input-radius);
  max-height: 50vh;
  max-width: 90vw;
  overflow-y: auto;
  overflow-x: hidden;
}

.opened + .mg-dropdown--content {
  display: block;
  opacity: 1;
  visibility: visible;
}

.mg-dropdown--icon {
  padding-right: 2.5rem !important;
}
.mg-dropdown--icon::after {
  vertical-align: middle;
  box-sizing: border-box;
  border: 2px solid transparent;
  border-color: currentColor;
  right: 1rem;
  border-right: 0;
  border-top: 0;
  content: "";
  display: inline-block;
  pointer-events: none;
  position: absolute;
  transform-origin: center;
  height: 0.8rem;
  width: 0.8rem;
  transition: all 0.2s;
  transform: rotate(-45deg);
  top: calc(50% - 0.5rem);
}
.mg-dropdown--icon.opened::after {
  transform: rotate(-225deg);
  top: 40%;
}

