  display: block;
}

.mg-collapse--icon {
  padding-right: 2.5rem !important;
  position: relative;
}
.mg-collapse--icon::after {
  vertical-align: middle;
  box-sizing: border-box;
  border: 2px solid transparent;
  border-color: currentColor;
  right: 1rem;
  border-right: 0;
  border-top: 0;
  content: "";
  display: inline-block;
  pointer-events: none;
  position: absolute;
  transform-origin: center;
  height: 0.8rem;
  width: 0.8rem;
  transition: all 0.2s;
  transform: rotate(-135deg);
  top: calc(50% - 0.4rem);
}
.mg-collapse--icon.opened::after {
  transform: rotate(-45deg);
  top: 40%;
}

